import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';

function Team() {
  const teamMembers = [
    { name: 'Dr. Phaedra Royle', role: 'Directrice de recherche' },
    { name: 'Guillaume Blais', role: 'Etudiant Phd' },
    { name: 'Maelys Pajon Roux', role: 'Etudiante Msc' },
    { name: 'Judicaël Fassaya', role: 'Etudiant Phd' },
    { name: 'Gabrielle Manning', role: 'Post doc' },
    { name: 'Noemie Olmand', role: 'Etudiante Mpo' },
    { name: 'Estelle Morissette', role: 'Etudiante Mpo' },
  ];

  return (
    <Container className="my-5">
      <h1>Notre équipe</h1>
      <br />
      <Row>
        {teamMembers.map((member, index) => (  
          <Col md={4} key={index}>
            <Card className="mb-4" style={{ boxShadow: '0px 2px 4px rgba(0,0,0,0.1)' }}>
              <Card.Body>
                <Card.Title>{member.name}</Card.Title>
                <Card.Text>{member.role}</Card.Text>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Team;